// @flow
import React, { useEffect } from 'react';
import clsx from 'clsx';
import type { Element, AbstractComponent } from 'react';
import { fetchSellerPropertyForms } from 'components/SellerProperty/Listing/Form/redux/Actions';
import { toastr } from 'react-redux-toastr';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import moment from 'moment';
import { QuestionListWidget } from './QuestionListWidget';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import { makeStyles } from '@material-ui/core/styles';

type Props = {
  properties: Array<Object>,
  fetchSellerPropertyForms: Function,
  error: string,
  isLoading: boolean,
};

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '100px',
  },
  loading: {
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.common.white,
  },
}));

export const QuestionList = (props: Props): Element<'div' | 'p'> => {
  const { properties, isLoading, fetchSellerPropertyForms, error } = props;
  const classes = useStyles();

  useEffect(() => {
    fetchSellerPropertyForms();
    // eslint-disable-next-line react-hooks/exhaustive-deps,
  }, []);

  useEffect(() => {
    if (error) {
      toastr.error('Something went wrong');
    }
  }, [error]);

  if (process.env.REACT_APP_SELL_SIDE_ENABLED !== '1') {
    return <p className="incorrect_parameter">Coming Soon</p>;
  }

  if (_isEmpty(properties) && !isLoading) {
    return <p className="incorrect_parameter">Property Not Found</p>;
  }

  const orderedProperties = properties.sort((itemA, itemB) => {
    return moment(itemA.CreatedAt) - moment(itemB.CreatedAt);
  });

  return (
    <BlockUi
      tag="div"
      blocking={isLoading}
      loader={<Loader active type="ball-pulse" color="rgba(231,28,99,1)" />}
      className={clsx(classes.container, { [classes.loading]: isLoading })}
    >
      {orderedProperties.map(property => {
        const { Forms: forms, PropertyId: propertyId } = property;
        return (
          <QuestionListWidget
            property={property}
            forms={forms}
            key={propertyId}
          />
        );
      })}
    </BlockUi>
  );
};

const structuredSelector = createStructuredSelector({
  properties: state =>
    _get(state, ['sellerPropertyForms', 'propertyForms', 'Properties'], []),
  isLoading: state => _get(state, ['sellerPropertyForms', 'isLoading']),
  error: state => _get(state, ['sellerPropertyForms', 'error']),
});

const mapDispatchToProps = { fetchSellerPropertyForms };

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(QuestionList): AbstractComponent<Props>);
