// @flow
import React from 'react';
import type { AbstractComponent, Element } from 'react';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import { QuestionItem } from './QuestionItem';
import { makeStyles } from '@material-ui/core/styles';

import { Widget } from 'components/Widget';
import { ConvertDTPropertyAddress } from 'shared/Consts';

type Props = {
  forms: Object,
  property: Object,
};

const useStyles = makeStyles(theme => ({
  widget: {
    marginBottom: theme.spacing(3),
  },
  description: {
    ...theme.typography.h5,
    marginBottom: theme.spacing(3),
    color: theme.palette.primary.gray,
  },
}));

const checkValidProps = ({ forms, property }) =>
  !_isEmpty(forms) && !_isEmpty(property);

export const QuestionListWidget = ({
  property,
  forms,
}: Props): Element<typeof Widget> => {
  const { Property: propertyData, PropertyId: propertyId } = property;
  const orderForms = forms.sort(
    (formA, formB) => _get(formA, ['Order'], 0) - _get(formB, ['Order'], 0),
  );
  const classes = useStyles();

  return (
    <Widget title="Question List" className={classes.widget}>
      <div className={classes.description}>
        To publish your properties (
        <strong>{ConvertDTPropertyAddress(propertyData)}</strong>) to our
        system, you need to complete the list of questions below:
      </div>
      {orderForms.map(form => (
        <QuestionItem
          form={form}
          propertyId={propertyId}
          key={_get(form, ['FormId'])}
        />
      ))}
    </Widget>
  );
};

export default (hideIfInvalidProps(checkValidProps)(
  QuestionListWidget,
): AbstractComponent<{}>);
