// @flow
import * as React from 'react';
import clsx from 'clsx';
import { FORM_STATUS } from 'components/FormBuilder/Constants';
import { makeStyles } from '@material-ui/core/styles';
import {
  CheckCircleOutline,
  RadioButtonUnchecked,
  ArrowForwardIos,
} from '@material-ui/icons';
import { Link } from 'react-router-dom';

type Props = {
  propertyId: number,
  form: Object,
};

const useStyles = makeStyles(theme => ({
  link: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    border: '1px solid #E0E0E0',
    backgroundColor: theme.palette.background.lightGray,
    padding: theme.spacing(2.125),
    marginBottom: theme.spacing(1),
    display: 'flex',
    alignItems: 'center',
  },
  name: {
    ...theme.typography.h5,
    marginLeft: theme.spacing(1),
    color: theme.palette.text.dark,
    flex: 1,
  },
  linkHighlight: {
    fontWeight: '900',
  },
  selectIcon: {
    float: 'right',
    width: '14px',
    height: '14px',
    color: theme.palette.primary.main,
  },
  checkIcon: {
    color: theme.palette.primary.main,
  },
  loopIcon: {
    color: theme.palette.primary.gray,
  },
}));

export const QuestionItem = (props: Props): React.Element<'div'> => {
  const { propertyId, form } = props;
  const { FormId: formId, Name: name, DataStatus: formStatus } = form;
  const isSubmitted = formStatus === FORM_STATUS.submitted;
  const classes = useStyles();

  return (
    <div data-testid="question-item">
      <Link
        className={classes.link}
        to={`/property?propertyId=${propertyId}&formId=${formId}`}
      >
        {isSubmitted ? (
          <CheckCircleOutline className={classes.checkIcon} />
        ) : (
          <RadioButtonUnchecked className={classes.loopIcon} />
        )}
        <span
          className={clsx(classes.name, {
            [classes.linkHighlight]: isSubmitted,
          })}
        >
          {name}
        </span>
        <ArrowForwardIos className={classes.selectIcon} />
      </Link>
    </div>
  );
};
